@keyframes fadeIn {
  from {
    visibility: hidden;
    transform: translateY(-80px);
  }
  to { 
    visibility: visible;
    transform: translateY(0);
  }
}

@keyframes fadeOut {
  from {
    visibility: visible;
    transform: translateY(0);
  }
  to { 
    visibility: hidden;
    transform: translateY(-80px);
  }
}


.nav-portfolio {
  // animation: 1s ease 0s 1 normal forwards running fadeIn;
  background-color: var(--main-green);
  // -webkit-animation: show-header 1s ease forwards;
  //   animation: show-header 1s ease forwards;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  position: fixed;
  width: 100vw;
  z-index: 2;
}

.nav-types {
  border-radius: 25px;
  display: flex;
  width: 15%;
  height: 50px;
  align-content: center;
  align-items: center;
}

.nav-portfolio h1 {
  cursor: pointer;
  position: relative;
  text-decoration: none;
  color: var(--main-white);
  margin-right: 50px;
  display: flex;
  align-items: center;
}

.nav-portfolio h1:hover {
  color: #274029;
}

.nav-portfolio h1:hover::before {
  content: '<';
  position: absolute;
  left: -20px;
}

.nav-portfolio h1:hover::after {
  content: '>';
  position: absolute;
  right: -20px;
}
